const getMimeTypePrefixIcon = (mimeType) => {
	if (mimeType) {
		const prefix = mimeType.split('/')[0];
		return MIME_PREFIX_ICON[prefix];
	}
};

export const getMimeTypeIcon = (mimeType, extension, unavailable) => {
	if (unavailable)
		return INFECTED_ICON;

	return MIME_ICON[mimeType] || getMimeTypePrefixIcon(mimeType) || EXTENSION_ICON[extension] || 'file';
};

class FileIcon extends React.PureComponent {
	render() {
		const {
			mimeType, extension, unavailable // for FileIcon
		} = this.props.properties;

		const type = getMimeTypeIcon(mimeType, extension, unavailable);
		return (
			<now-legacy-icon
				type={type}
				{...pick(this.props.properties, iconProps)} // pass through for eds-legacy-icon
			/>
		);
	}
}

export default FileIcon;

